<template>
  <div id="app">
    <canvas ref="threejs" />
    <div class="controls">
      <button @click="changeBackground('/abaaa33.jpg')">light</button>
      <button @click="changeBackground('/abaaa33666.jpg')">dark</button>
    </div>
  </div>
</template>

<script>
import * as Three from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
// import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';

// import OrbitControls from 'three/examples/js/controls/OrbitControls.js';

export default {
  name: 'App',
  data() {
    return {
      backgroundSrc: '/abaaa33.jpg',
      scene: null,
      camera: null,
      renderer: null,
      geometry: null,
      material: null,
      mesh: null,
      loader: null,
      controls: null,
      manager: null,
      object: null,
      objLoader: null,
      light: null,
      materialList: [],
      model: null
    }
  },
  methods: {
    init() {
      // window.THREE = Three;
      this.scene = new Three.Scene();
      // window.scene = this.scene;
      this.camera = new Three.PerspectiveCamera(75, window.innerWidth/window.innerHeight, 0.1, 10000);
      this.camera.position.x = 1;
      this.camera.lookAt(1,0,4);
      this.declareRenderer();
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.enableZoom = false;

      this.addLight();
      this.renderShowroom();

      this.loader = new GLTFLoader();
      this.loader.load('/8afb282373834deda783f685c450f4c9.glb', (obj) => {
        // const coords = new Three.Box3().setFromObject(obj.scene).getSize(new Three.Vector3());
        // const maxCoord = Math.max.apply(null, Object.values(coords));
        // this.controls.minDistance = maxCoord
        obj.scene.position.x = 1;
        obj.scene.position.z = 6;
        obj.scene.position.y = 0;
        obj.scene.scale.set(4,4,4);
        // obj.scene.on('click', (el)=> console.log(el));
        // this.controls.maxDistance = maxCoord * 3;
        this.model = obj.scene;
        this.scene.add(this.model);
      });
    },
    addLight() {
      this.light = new Three.PointLight(0xffffff, 2 ,100)
      this.light.position.set(5, 5, 5);
      this.scene.add(this.light)
    },
    renderShowroom() {
      const textureLoader = new Three.TextureLoader();
      const texture = textureLoader.load(this.backgroundSrc);
      const material = new Three.MeshBasicMaterial({
        map: texture,
      });
      material.side = Three.DoubleSide

      const fbxLoader = new FBXLoader();
      fbxLoader.load('/SPHEREforIVANFBXCP.fbx', (obj) => {
        obj.traverse(node => {
          if (node.isMesh) {
            node.material = material;
          }
        });
        this.scene.add(obj)
      });
    },
    addSkybox() {
      const texture_bk = new Three.TextureLoader().load('/arid_bk.jpg');
      const texture_dn = new Three.TextureLoader().load('/arid_dn.jpg');
      const texture_ft = new Three.TextureLoader().load('/arid_ft.jpg');
      const texture_lf = new Three.TextureLoader().load('/arid_lf.jpg');
      const texture_rt = new Three.TextureLoader().load('/arid_rt.jpg');
      const texture_up = new Three.TextureLoader().load('/arid_up.jpg');

      // const texture_bk = new Three.TextureLoader().load('/CAMERAback.jpg');
      // const texture_dn = new Three.TextureLoader().load('/CAMERAdown.jpg');
      // const texture_ft = new Three.TextureLoader().load('/CAMERAfront.jpg');
      // const texture_lf = new Three.TextureLoader().load('/CAMERAleft.jpg');
      // const texture_rt = new Three.TextureLoader().load('/CAMERAright.jpg');
      // const texture_up = new Three.TextureLoader().load('/CAMERAup.jpg');

      this.materialList.push(new Three.MeshBasicMaterial({map: texture_ft}));
      this.materialList.push(new Three.MeshBasicMaterial({map: texture_bk}));
      this.materialList.push(new Three.MeshBasicMaterial({map: texture_up}));
      this.materialList.push(new Three.MeshBasicMaterial({map: texture_dn}));
      this.materialList.push(new Three.MeshBasicMaterial({map: texture_rt}));
      this.materialList.push(new Three.MeshBasicMaterial({map: texture_lf}));

      this.materialList = this.materialList.map(el => {
        el.side = Three.BackSide
        return el;
      });

      let skyboxGeo = new Three.BoxGeometry(1000, 1000 , 1000);
      let skybox = new Three.Mesh(skyboxGeo, this.materialList);
      this.scene.add(skybox);
    },
    declareRenderer() {
      this.renderer = new Three.WebGLRenderer({
        antialias: true,
        canvas: this.$refs.threejs,
      });
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.renderer.setClearColor(0xFFFFFF);
    },
    changeBackground(src) {
      this.backgroundSrc = src;
      this.renderShowroom();
    },
    animate() {
      requestAnimationFrame(this.animate);
      if (this.model?.rotation?.x) {
        this.model.rotation.x += 0.005;
      }
      this.controls.update();
      this.renderer.render(this.scene, this.camera);
    },
    // addMouseHandle() {
    //   document.addEventListener('mousemove', this.onDocumentMouseDown, false);
    //   document.addEventListener('mouseup', this.onDocumentMouseUp, false);
    // },
    // onDocumentMouseDown(e) {
    //   console.log(e);
    //   // this.camera.position.y = this.camera.target.y + 0.5;
    // },
    // onDocumentMouseUp() {
    //
    // }
  },
  mounted() {
    this.init();
    this.animate();
    // this.addMouseHandle();
  }
}
</script>

<style>
body {
  margin: 0;
  padding: 0;
}
.controls {
  position: absolute;
  z-index: 3;
  top: 20px;
  left: 20px;
}
</style>
